import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Grid,
  Container,
  Skeleton,
  Alert,
  Button,
  Chip,
  Paper,
  TextField,
  MenuItem,
  Pagination,
  Stack,
  alpha,
  useTheme
} from '@mui/material';
import { styled } from '@mui/material/styles';
import api from '../../api/api';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import LocalGasStationIcon from '@mui/icons-material/LocalGasStation';
import SpeedIcon from '@mui/icons-material/Speed';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import SortIcon from '@mui/icons-material/Sort';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import SearchIcon from '@mui/icons-material/Search';

// Soft UI Styled Components
const HeroSection = styled(Box)(({ theme }) => ({
  background: theme.palette.mode === 'light'
    ? 'linear-gradient(135deg, rgba(79, 70, 229, 0.08) 0%, rgba(147, 51, 234, 0.08) 100%)'
    : 'linear-gradient(135deg, rgba(99, 102, 241, 0.15) 0%, rgba(168, 85, 247, 0.15) 100%)',
  borderRadius: 24,
  padding: theme.spacing(6, 4),
  marginBottom: theme.spacing(4),
  backdropFilter: 'blur(10px)',
  border: `1px solid ${theme.palette.divider}`,
  [theme.breakpoints.down('md')]: {
    padding: theme.spacing(4, 3),
  },
}));

const StyledCard = styled(Card)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  borderRadius: 20,
  backdropFilter: 'blur(20px)',
  backgroundColor: theme.palette.mode === 'light'
    ? 'rgba(255, 255, 255, 0.8)'
    : 'rgba(30, 41, 59, 0.8)',
  border: `1px solid ${theme.palette.mode === 'light' ? 'rgba(148, 163, 184, 0.1)' : 'rgba(148, 163, 184, 0.05)'}`,
  transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
  '&:hover': {
    transform: 'translateY(-8px)',
    boxShadow: theme.palette.mode === 'light'
      ? '0px 20px 50px rgba(15, 23, 42, 0.15)'
      : '0px 20px 50px rgba(0, 0, 0, 0.5)',
  },
}));

const StyledCardMedia = styled(CardMedia)(({ theme }) => ({
  paddingTop: '60%',
  position: 'relative',
  borderRadius: '20px 20px 0 0',
  overflow: 'hidden',
  '&::after': {
    content: '""',
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    height: '40%',
    background: 'linear-gradient(to top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%)',
  },
}));

const PriceTag = styled(Paper)(({ theme }) => ({
  position: 'absolute',
  bottom: 16,
  right: 16,
  padding: '8px 16px',
  borderRadius: 12,
  background: theme.palette.mode === 'light'
    ? 'linear-gradient(135deg, #4F46E5 0%, #7C3AED 100%)'
    : 'linear-gradient(135deg, #6366F1 0%, #8B5CF6 100%)',
  color: '#FFFFFF',
  fontWeight: 700,
  zIndex: 1,
  display: 'flex',
  alignItems: 'center',
  gap: 4,
  backdropFilter: 'blur(10px)',
  boxShadow: theme.palette.mode === 'light'
    ? '0px 4px 12px rgba(79, 70, 229, 0.4)'
    : '0px 4px 12px rgba(99, 102, 241, 0.5)',
}));

const FilterPanel = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(3),
  marginBottom: theme.spacing(4),
  borderRadius: 20,
  backdropFilter: 'blur(20px)',
  backgroundColor: theme.palette.mode === 'light'
    ? 'rgba(255, 255, 255, 0.7)'
    : 'rgba(30, 41, 59, 0.7)',
  border: `1px solid ${theme.palette.divider}`,
  boxShadow: theme.palette.mode === 'light'
    ? '0px 8px 24px rgba(15, 23, 42, 0.08)'
    : '0px 8px 24px rgba(0, 0, 0, 0.3)',
}));

// Options de filtrage
const FILTER_OPTIONS = {
  make: ['Toutes', 'Peugeot', 'Renault', 'Volkswagen', 'BMW', 'Audi', 'Mercedes'],
  priceRange: [
    { value: '0-5000', label: 'Moins de 5 000 ' },
    { value: '5000-10000', label: '5 000  - 10 000 ' },
    { value: '10000-20000', label: '10 000  - 20 000 ' },
    { value: '20000-50000', label: '20 000  - 50 000 ' },
    { value: '50000-100000', label: '50 000  +' },
  ],
  sortBy: [
    { value: 'price-asc', label: 'Prix croissant' },
    { value: 'price-desc', label: 'Prix dcroissant' },
    { value: 'year-desc', label: 'Plus rcent' },
    { value: 'mileage-asc', label: 'Kilomtrage (bas)' },
  ]
};

function HomePage() {
  // tats
  const [cars, setCars] = useState([]);
  const [filteredCars, setFilteredCars] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [page, setPage] = useState(1);
  const [filters, setFilters] = useState({
    make: 'Toutes',
    priceRange: '',
    sortBy: 'price-asc',
    searchQuery: ''
  });

  // Constantes de pagination
  const CARS_PER_PAGE = 9;
  const pageCount = Math.ceil(filteredCars.length / CARS_PER_PAGE);
  const paginatedCars = filteredCars.slice(
    (page - 1) * CARS_PER_PAGE,
    page * CARS_PER_PAGE
  );

  // Chargement des voitures
  useEffect(() => {
    async function fetchCars() {
      try {
        setLoading(true);
        const response = await api.get('/cars');
        setCars(response.data);
        setFilteredCars(response.data);
      } catch (err) {
        console.error('Error fetching cars:', err);
        setError('Une erreur est survenue lors du chargement des vhicules. Veuillez ressayer plus tard.');
      } finally {
        setLoading(false);
      }
    }
    fetchCars();
  }, []);

  // Filtrage des voitures
  useEffect(() => {
    let result = [...cars];

    // Filtre par marque
    if (filters.make && filters.make !== 'Toutes') {
      result = result.filter(car => car.make === filters.make);
    }

    // Filtre par fourchette de prix
    if (filters.priceRange) {
      const [min, max] = filters.priceRange.split('-').map(Number);
      result = result.filter(car => car.price >= min && car.price <= max);
    }

    // Recherche par texte
    if (filters.searchQuery) {
      const query = filters.searchQuery.toLowerCase();
      result = result.filter(car =>
        `${car.make} ${car.model} ${car.series || ''}`
          .toLowerCase()
          .includes(query)
      );
    }

    // Tri
    result.sort((a, b) => {
      const [field, order] = filters.sortBy.split('-');
      const modifier = order === 'asc' ? 1 : -1;
      return (a[field] - b[field]) * modifier;
    });

    setFilteredCars(result);
    setPage(1); // Rinitialiser  la premire page lors du filtrage
  }, [filters, cars]);

  // Gestion des changements de filtre
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Formatage du prix
  const formatPrice = (price) => {
    return new Intl.NumberFormat('fr-FR', {
      style: 'currency',
      currency: 'EUR',
      maximumFractionDigits: 0
    }).format(price);
  };

  // cran de chargement
  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Chargement des vhicules...
        </Typography>
        <Grid container spacing={4}>
          {[...Array(6)].map((_, index) => (
            <Grid item key={index} xs={12} sm={6} md={4}>
              <StyledCard>
                <Skeleton variant="rectangular" height={200} animation="wave" />
                <CardContent>
                  <Skeleton width="80%" height={32} animation="wave" />
                  <Skeleton width="60%" height={24} animation="wave" sx={{ mt: 1 }} />
                  <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
                    <Skeleton width="40%" height={20} animation="wave" />
                    <Skeleton width="40%" height={20} animation="wave" />
                  </Box>
                  <Skeleton width="100%" height={40} sx={{ mt: 2 }} animation="wave" />
                </CardContent>
              </StyledCard>
            </Grid>
          ))}
        </Grid>
      </Container>
    );
  }

  // Gestion des erreurs
  if (error) {
    return (
      <Container maxWidth="md" sx={{ py: 4, textAlign: 'center' }}>
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
        <Button
          variant="contained"
          onClick={() => window.location.reload()}
        >
          Ressayer
        </Button>
      </Container>
    );
  }

  // Aucune voiture disponible
  if (filteredCars.length === 0) {
    return (
      <Container maxWidth="md" sx={{ py: 8, textAlign: 'center' }}>
        <DirectionsCarIcon sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
        <Typography variant="h5" color="text.secondary" gutterBottom>
          Aucun vhicule ne correspond  vos critres
        </Typography>
        <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
          Essayez de modifier vos filtres ou de rinitialiser la recherche.
        </Typography>
        <Button
          variant="outlined"
          onClick={() => setFilters({
            make: 'Toutes',
            priceRange: '',
            sortBy: 'price-asc',
            searchQuery: ''
          })}
        >
          Rinitialiser les filtres
        </Button>
      </Container>
    );
  }

  // Rendu principal
  const theme = useTheme();

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      {/* Hero Section */}
      <HeroSection>
        <Typography
          variant="h2"
          component="h1"
          gutterBottom
          sx={{
            fontFamily: 'Outfit, sans-serif',
            fontWeight: 800,
            background: (theme) =>
              theme.palette.mode === 'light'
                ? 'linear-gradient(135deg, #4F46E5 0%, #7C3AED 100%)'
                : 'linear-gradient(135deg, #6366F1 0%, #8B5CF6 100%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            backgroundClip: 'text',
            mb: 2,
          }}
        >
          Nos vhicules d'occasion
        </Typography>
        <Typography
          variant="h6"
          color="text.secondary"
          sx={{
            maxWidth: 600,
            fontWeight: 500,
          }}
        >
          Dcouvrez notre slection de vhicules de qualit, soigneusement inspects et prts  prendre la route.
        </Typography>
      </HeroSection>

      {/* Barre de recherche et filtres */}
      <FilterPanel elevation={0}>
        <Grid container spacing={3} alignItems="center">
          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              placeholder="Rechercher une voiture..."
              variant="outlined"
              value={filters.searchQuery}
              onChange={(e) => setFilters(prev => ({ ...prev, searchQuery: e.target.value }))}
              InputProps={{
                startAdornment: <SearchIcon color="action" sx={{ mr: 1 }} />,
              }}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 3,
                },
              }}
            />
          </Grid>

          <Grid item xs={12} sm={6} md={2}>
            <TextField
              select
              fullWidth
              label="Marque"
              value={filters.make}
              onChange={handleFilterChange}
              name="make"
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 3,
                },
              }}
            >
              {FILTER_OPTIONS.make.map((make) => (
                <MenuItem key={make} value={make}>
                  {make}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <TextField
              select
              fullWidth
              label="Prix"
              value={filters.priceRange}
              onChange={handleFilterChange}
              name="priceRange"
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 3,
                },
              }}
            >
              <MenuItem value="">Tous les prix</MenuItem>
              {FILTER_OPTIONS.priceRange.map((range) => (
                <MenuItem key={range.value} value={range.value}>
                  {range.label}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} sm={6} md={3}>
            <TextField
              select
              fullWidth
              label="Trier par"
              value={filters.sortBy}
              onChange={handleFilterChange}
              name="sortBy"
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 3,
                },
              }}
            >
              {FILTER_OPTIONS.sortBy.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
        </Grid>
      </FilterPanel>

      {/* Affichage du nombre de rsultats */}
      <Typography
        variant="h6"
        color="text.secondary"
        sx={{
          mb: 3,
          fontWeight: 600,
        }}
      >
        {filteredCars.length} vhicule{filteredCars.length > 1 ? 's' : ''} trouv{filteredCars.length > 1 ? 's' : ''}
      </Typography>

      {/* Grille des voitures */}
      <Grid container spacing={4}>
        {paginatedCars.map((car) => {
          const fullName = `${car.year} ${car.make} ${car.model} ${car.series || ''}`.trim();
          const imageUrl = car.photos?.[0] || 'https://source.unsplash.com/random/600x400/?car';

          return (
            <Grid item key={car.id} xs={12} sm={6} md={4}>
              <StyledCard>
                <Link to={`/cars/${car.id}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                  <Box sx={{ position: 'relative' }}>
                    <StyledCardMedia
                      image={imageUrl}
                      title={fullName}
                    />
                    <PriceTag>
                      <AttachMoneyIcon fontSize="small" />
                      {formatPrice(car.price)}
                    </PriceTag>
                  </Box>
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Typography variant="h6" component="h2" noWrap>
                        {car.make} {car.model}
                      </Typography>
                      <Chip
                        label={car.condition === 'new' ? 'Neuf' : 'Occasion'}
                        size="small"
                        color={car.condition === 'new' ? 'primary' : 'secondary'}
                      />
                    </Box>

                    <Typography variant="subtitle2" color="text.secondary" gutterBottom>
                      {car.year}  {car.mileage?.toLocaleString()} km
                    </Typography>

                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1, flexWrap: 'wrap', gap: 1 }}>
                      <Chip
                        icon={<LocalGasStationIcon fontSize="small" />}
                        label={car.fuelType || 'N/C'}
                        size="small"
                        variant="outlined"
                      />
                      <Chip
                        icon={<SpeedIcon fontSize="small" />}
                        label={car.transmission || 'N/C'}
                        size="small"
                        variant="outlined"
                      />
                    </Box>

                    <Button
                      variant="outlined"
                      size="small"
                      component={Link}
                      to={`/cars/${car.id}`}
                      fullWidth
                      sx={{ mt: 2 }}
                    >
                      Voir les dtails
                    </Button>
                  </CardContent>
                </Link>
              </StyledCard>
            </Grid>
          );
        })}
      </Grid>

      {/* Pagination */}
      {pageCount > 1 && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <Pagination
            count={pageCount}
            page={page}
            onChange={(_, value) => setPage(value)}
            color="primary"
            showFirstButton
            showLastButton
          />
        </Box>
      )}
    </Container>
  );
}

export default HomePage;