import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';
import { useThemeMode } from '../../context/ThemeContext';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  Container,
  Avatar,
  IconButton,
  useTheme,
  alpha
} from '@mui/material';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';

export default function Navbar() {
  const { currentUser, logout } = useAuth();
  const { mode, toggleTheme } = useThemeMode();
  const theme = useTheme();
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/');
    } catch (error) {
      console.error("Failed to log out", error);
    }
  };

  return (
    <AppBar
      position="sticky"
      elevation={0}
      sx={{
        backdropFilter: 'blur(20px)',
        background: (theme) =>
          theme.palette.mode === 'light'
            ? 'linear-gradient(135deg, rgba(79, 70, 229, 0.95) 0%, rgba(124, 58, 237, 0.95) 100%)'
            : 'linear-gradient(135deg, rgba(99, 102, 241, 0.95) 0%, rgba(139, 92, 246, 0.95) 100%)',
        boxShadow: (theme) =>
          theme.palette.mode === 'light'
            ? '0px 4px 20px rgba(79, 70, 229, 0.25)'
            : '0px 4px 20px rgba(99, 102, 241, 0.35)',
        borderBottom: (theme) => `1px solid ${alpha(theme.palette.common.white, 0.1)}`,
      }}
    >
      <Container maxWidth="lg">
        <Toolbar disableGutters sx={{ minHeight: { xs: 64, md: 72 } }}>
          {/* Logo */}
          <DirectionsCarIcon
            sx={{
              display: { xs: 'none', md: 'flex' },
              mr: 1.5,
              fontSize: 32,
              filter: 'drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.2))',
            }}
          />
          <Typography
            variant="h5"
            noWrap
            component={Link}
            to="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'Outfit, sans-serif',
              fontWeight: 800,
              color: 'inherit',
              textDecoration: 'none',
              letterSpacing: '-0.02em',
              textShadow: '0px 2px 8px rgba(0, 0, 0, 0.2)',
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-1px)',
                textShadow: '0px 4px 12px rgba(0, 0, 0, 0.3)',
              },
            }}
          >
            AutoMarket
          </Typography>

          {/* Mobile Logo */}
          <DirectionsCarIcon
            sx={{
              display: { xs: 'flex', md: 'none' },
              mr: 1,
              fontSize: 28,
            }}
          />
          <Typography
            variant="h6"
            noWrap
            component={Link}
            to="/"
            sx={{
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'Outfit, sans-serif',
              fontWeight: 700,
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            AutoMarket
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'block' } }} />

          {/* Navigation Links */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1.5 }}>
            <Button
              component={Link}
              to="/"
              color="inherit"
              sx={{
                textTransform: 'none',
                fontWeight: 600,
                fontSize: '0.9375rem',
                px: 2,
                py: 1,
                borderRadius: 2,
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: alpha(theme.palette.common.white, 0.15),
                  transform: 'translateY(-2px)',
                },
              }}
            >
              Accueil
            </Button>

            {currentUser ? (
              <>
                <Button
                  component={Link}
                  to="/admin/dashboard"
                  color="inherit"
                  sx={{
                    textTransform: 'none',
                    fontWeight: 600,
                    fontSize: '0.9375rem',
                    px: 2,
                    py: 1,
                    borderRadius: 2,
                    transition: 'all 0.3s ease',
                    display: { xs: 'none', sm: 'inline-flex' },
                    '&:hover': {
                      backgroundColor: alpha(theme.palette.common.white, 0.15),
                      transform: 'translateY(-2px)',
                    },
                  }}
                >
                  Tableau de bord
                </Button>
                <Button
                  onClick={handleLogout}
                  color="inherit"
                  variant="outlined"
                  sx={{
                    textTransform: 'none',
                    fontWeight: 600,
                    fontSize: '0.9375rem',
                    px: 2.5,
                    py: 1,
                    borderRadius: 2,
                    borderColor: alpha(theme.palette.common.white, 0.3),
                    borderWidth: 2,
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      borderWidth: 2,
                      borderColor: theme.palette.common.white,
                      backgroundColor: alpha(theme.palette.common.white, 0.15),
                      transform: 'translateY(-2px)',
                    }
                  }}
                >
                  Dconnexion
                </Button>
                <Avatar
                  sx={{
                    bgcolor: 'white',
                    color: 'primary.main',
                    width: 40,
                    height: 40,
                    fontSize: '1rem',
                    fontWeight: 'bold',
                    boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.2)',
                    border: `2px solid ${alpha(theme.palette.common.white, 0.3)}`,
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      transform: 'scale(1.1)',
                      boxShadow: '0px 6px 16px rgba(0, 0, 0, 0.3)',
                    },
                  }}
                >
                  {currentUser.email.charAt(0).toUpperCase()}
                </Avatar>
              </>
            ) : (
              <Button
                component={Link}
                to="/admin/login"
                color="inherit"
                variant="outlined"
                sx={{
                  textTransform: 'none',
                  fontWeight: 600,
                  fontSize: '0.9375rem',
                  px: 2.5,
                  py: 1,
                  borderRadius: 2,
                  borderColor: alpha(theme.palette.common.white, 0.3),
                  borderWidth: 2,
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    borderWidth: 2,
                    borderColor: theme.palette.common.white,
                    backgroundColor: alpha(theme.palette.common.white, 0.15),
                    transform: 'translateY(-2px)',
                  }
                }}
              >
                Connexion Admin
              </Button>
            )}

            {/* Theme Toggle */}
            <IconButton
              onClick={toggleTheme}
              color="inherit"
              sx={{
                ml: 1,
                width: 40,
                height: 40,
                borderRadius: 2,
                backgroundColor: alpha(theme.palette.common.white, 0.1),
                transition: 'all 0.3s ease',
                '&:hover': {
                  backgroundColor: alpha(theme.palette.common.white, 0.2),
                  transform: 'rotate(180deg) scale(1.1)',
                },
              }}
              aria-label="toggle theme"
            >
              {mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
            </IconButton>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
