import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import api from '../../api/api.js';

function EditCar() {
  const { id } = useParams();
  const navigate = useNavigate();

  const [form, setForm] = useState({
    year: '',
    make: '',
    model: '',
    series: '',
    mileage: '',
    location_city: '',
    location_branch: '',
    sale_price: '',
    stock_number: '',
    vin: '',
    transmission: '',
    fuel_type: '',
    cylinders: '',
    photos: [],
  });
  const [loading, setLoading] = useState(true);
  const [errors, setErrors] = useState([]);
  const [submitting, setSubmitting] = useState(false);
  const [fetchError, setFetchError] = useState(null);

  useEffect(() => {
    async function fetchCar() {
      try {
        setLoading(true);
        const response = await api.get(`/cars/${id}`);
        if (!response.data) {
          setFetchError('Voiture introuvable');
          return;
        }
        const car = response.data;
        setForm({
          year: car.year.toString(),
          make: car.make,
          model: car.model,
          series: car.series,
          mileage: car.mileage.toString(),
          location_city: car.location_city,
          location_branch: car.location_branch,
          sale_price: car.sale_price.toString(),
          stock_number: car.stock_number,
          vin: car.vin,
          transmission: car.transmission,
          fuel_type: car.fuel_type,
          cylinders: car.cylinders.toString(),
          photos: Array.isArray(car.photos) && car.photos.length > 0 ? car.photos : [''],
        });
      } catch {
        setFetchError('Erreur lors du chargement des donnes');
      } finally {
        setLoading(false);
      }
    }
    fetchCar();
  }, [id]);

  function handleChange(e) {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  }

  async function handlePhotoUpload(e) {
    const files = Array.from(e.target.files);
    const newPhotos = [...form.photos];
    
    for (const file of files) {
      const formData = new FormData();
      formData.append('photo', file);
      
      try {
        const response = await api.post('/api/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        newPhotos.push(response.data.url);
      } catch (error) {
        console.error('Error uploading photo:', error);
        setErrors(prev => [...prev, `Erreur lors du tlversement de l'image: ${file.name}`]);
      }
    }
    
    setForm(prev => ({ ...prev, photos: newPhotos }));
  }

  function addPhotoField() {
    setForm((prev) => ({ ...prev, photos: [...prev.photos, ''] }));
  }

  function removePhotoField(index) {
    setForm((prev) => {
      const newPhotos = prev.photos.filter((_, i) => i !== index);
      return { ...prev, photos: newPhotos.length > 0 ? newPhotos : [''] };
    });
  }

  function validate() {
    const errs = [];

    const year = parseInt(form.year, 10);
    if (isNaN(year) || year < 1886 || year > new Date().getFullYear() + 1) {
      errs.push('Anne invalide');
    }
    if (!form.make.trim()) errs.push('Marque obligatoire');
    if (!form.model.trim()) errs.push('Modle obligatoire');
    if (!form.series.trim()) errs.push('Srie obligatoire');

    const mileage = parseInt(form.mileage, 10);
    if (isNaN(mileage) || mileage < 0) {
      errs.push('Kilomtrage invalide');
    }

    if (!form.location_city.trim()) errs.push('Ville obligatoire');
    if (!form.location_branch.trim()) errs.push('Branche obligatoire');

    const salePrice = parseFloat(form.sale_price);
    if (isNaN(salePrice) || salePrice < 0) {
      errs.push('Prix de vente invalide');
    }

    if (!form.stock_number.trim()) errs.push('Numro de stock obligatoire');
    if (!form.vin.trim()) errs.push('VIN obligatoire');
    if (!form.transmission.trim()) errs.push('Transmission obligatoire');
    if (!form.fuel_type.trim()) errs.push('Type de carburant obligatoire');

    const cylinders = parseInt(form.cylinders, 10);
    if (isNaN(cylinders) || cylinders <= 0) {
      errs.push('Nombre de cylindres invalide');
    }

    if (!Array.isArray(form.photos) || form.photos.length === 0) {
      errs.push('Au moins une photo est requise');
    }

    return errs;
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setErrors([]);
    const validationErrors = validate();
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      return;
    }
    setSubmitting(true);
    try {
      const carData = {
        year: parseInt(form.year, 10),
        make: form.make.trim(),
        model: form.model.trim(),
        series: form.series.trim(),
        mileage: parseInt(form.mileage, 10),
        location_city: form.location_city.trim(),
        location_branch: form.location_branch.trim(),
        sale_price: parseFloat(form.sale_price),
        stock_number: form.stock_number.trim(),
        vin: form.vin.trim(),
        transmission: form.transmission.trim(),
        fuel_type: form.fuel_type.trim(),
        cylinders: parseInt(form.cylinders, 10),
        photos: form.photos.map((p) => p.trim()),
      };
      await api.put(`/cars/${id}`, carData);
      navigate('/admin/dashboard');
    } catch {
      setErrors(['Erreur lors de la mise  jour de la voiture.']);
    } finally {
      setSubmitting(false);
    }
  }

  if (loading) return <p>Chargement des donnes...</p>;
  if (fetchError) return <p>{fetchError}</p>;

  return (
    <div style={{ maxWidth: 600, margin: 'auto', padding: '1rem' }}>
      <h1>Modifier la voiture</h1>
      <form onSubmit={handleSubmit} noValidate>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Anne
            <input
              type="number"
              name="year"
              value={form.year}
              onChange={handleChange}
              min="1886"
              max={new Date().getFullYear() + 1}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Marque
            <input
              type="text"
              name="make"
              value={form.make}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Modle
            <input
              type="text"
              name="model"
              value={form.model}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Srie
            <input
              type="text"
              name="series"
              value={form.series}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Kilomtrage
            <input
              type="number"
              name="mileage"
              value={form.mileage}
              onChange={handleChange}
              min="0"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Ville
            <input
              type="text"
              name="location_city"
              value={form.location_city}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Branche
            <input
              type="text"
              name="location_branch"
              value={form.location_branch}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Prix de vente
            <input
              type="number"
              name="sale_price"
              value={form.sale_price}
              onChange={handleChange}
              min="0"
              step="0.01"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Numro de stock
            <input
              type="text"
              name="stock_number"
              value={form.stock_number}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            VIN
            <input
              type="text"
              name="vin"
              value={form.vin}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Transmission
            <input
              type="text"
              name="transmission"
              value={form.transmission}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
              placeholder="Ex: Automatique, Manuelle"
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Type de carburant
            <input
              type="text"
              name="fuel_type"
              value={form.fuel_type}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
              placeholder="Ex: Essence, Diesel"
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Cylindres
            <input
              type="number"
              name="cylinders"
              value={form.cylinders}
              onChange={handleChange}
              min="1"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2">
            Photos
          </label>
          <input
            type="file"
            accept="image/*"
            multiple
            onChange={handlePhotoUpload}
            className="mb-4"
          />
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4">
            {form.photos.map((url, index) => (
              <div key={index} className="relative">
                <img 
                  src={url} 
                  alt={`Voiture ${index + 1}`}
                  className="w-full h-48 object-cover rounded"
                />
                <button
                  type="button"
                  onClick={() => removePhotoField(index)}
                  className="absolute top-2 right-2 bg-red-500 hover:bg-red-700 text-white rounded-full w-8 h-8 flex items-center justify-center"
                  title="Supprimer cette photo"
                >
                  
                </button>
              </div>
            ))}
          </div>
          {form.photos.length === 0 && (
            <p className="text-sm text-gray-500 mt-2">
              Aucune photo slectionne. Veuillez tlverser au moins une photo.
            </p>
          )}
        </div>

        {errors.length > 0 && (
          <ul style={{ color: 'red', marginTop: '1rem' }}>
            {errors.map((err, i) => (
              <li key={i}>{err}</li>
            ))}
          </ul>
        )}

        <button type="submit" disabled={submitting} style={{ marginTop: '1rem', padding: '0.75rem 1.5rem' }}>
          {submitting ? 'Mise  jour en cours...' : 'Mettre  jour la voiture'}
        </button>
      </form>
    </div>
  );
}

export default EditCar;
