import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import api from '../../api/api.js';

const initialFormState = {
  year: '',
  make: '',
  model: '',
  series: '',
  mileage: '',
  location_city: '',
  location_branch: '',
  sale_price: '',
  stock_number: '',
  vin: '',
  transmission: '',
  fuel_type: '',
  cylinders: '',
  photos: [],
};

function CreateCar() {
  const [form, setForm] = useState(initialFormState);
  const [errors, setErrors] = useState([]);
  const [submitting, setSubmitting] = useState(false);
  const navigate = useNavigate();

  function handleChange(e) {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  }

  async function handlePhotoUpload(e) {
    const files = Array.from(e.target.files);
    if (files.length === 0) return;
    
    const newPhotos = [...form.photos];
    const uploadErrors = [];
    
    for (const file of files) {
      // Vrifier la taille du fichier (max 10MB)
      if (file.size > 10 * 1024 * 1024) {
        uploadErrors.push(`Le fichier ${file.name} dpasse la taille maximale de 10MB`);
        continue;
      }
      
      const formData = new FormData();
      formData.append('photo', file);
      
      try {
        const response = await api.post('/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        // S'assurer que l'URL est bien une URL absolue
        let imageUrl = response.data.url;
        console.log('URL reue du serveur:', imageUrl);
        
        // Si l'URL est relative, on la transforme en absolue
        if (!imageUrl.startsWith('http')) {
          // S'assurer qu'il n'y a pas de double slash
          const baseUrl = window.location.origin.endsWith('/') ? 
                         window.location.origin.slice(0, -1) : window.location.origin;
          imageUrl = `${baseUrl}${imageUrl.startsWith('/') ? '' : '/'}${imageUrl}`;
        }
        
        console.log('URL finale de l\'image:', imageUrl);
        
        newPhotos.push(imageUrl);
      } catch (error) {
        console.error('Erreur lors du tlversement:', error);
        uploadErrors.push(`Erreur lors du tlversement de l'image ${file.name}: ${error.response?.data?.message || error.message}`);
      }
    }
    
    if (uploadErrors.length > 0) {
      setErrors(prev => [...prev, ...uploadErrors]);
    }
    
    if (newPhotos.length > 0) {
      setForm(prev => ({ ...prev, photos: newPhotos }));
    }
  }

  function addPhotoField() {
    setForm((prev) => ({ ...prev, photos: [...prev.photos, ''] }));
  }

  function removePhotoField(index) {
    setForm((prev) => {
      const newPhotos = prev.photos.filter((_, i) => i !== index);
      return { ...prev, photos: newPhotos };
    });
  }

  function validate() {
    const errs = [];

    const year = parseInt(form.year, 10);
    if (isNaN(year) || year < 1886 || year > new Date().getFullYear() + 1) {
      errs.push('Anne invalide');
    }
    if (!form.make.trim()) errs.push('Marque obligatoire');
    if (!form.model.trim()) errs.push('Modle obligatoire');
    if (!form.series.trim()) errs.push('Srie obligatoire');

    const mileage = parseInt(form.mileage, 10);
    if (isNaN(mileage) || mileage < 0) {
      errs.push('Kilomtrage invalide');
    }

    if (!form.location_city.trim()) errs.push('Ville obligatoire');
    if (!form.location_branch.trim()) errs.push('Branche obligatoire');

    const salePrice = parseFloat(form.sale_price);
    if (isNaN(salePrice) || salePrice < 0) {
      errs.push('Prix de vente invalide');
    }

    if (!form.stock_number.trim()) errs.push('Numro de stock obligatoire');
    if (!form.vin.trim()) errs.push('VIN obligatoire');
    if (!form.transmission.trim()) errs.push('Transmission obligatoire');
    if (!form.fuel_type.trim()) errs.push('Type de carburant obligatoire');

    const cylinders = parseInt(form.cylinders, 10);
    if (isNaN(cylinders) || cylinders <= 0) {
      errs.push('Nombre de cylindres invalide');
    }

    if (!Array.isArray(form.photos) || form.photos.length === 0) {
      errs.push('Au moins une photo est requise');
    }

    return errs;
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setErrors([]);
    const validationErrors = validate();
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      return;
    }
    setSubmitting(true);
    try {
      const carData = {
        year: parseInt(form.year, 10),
        make: form.make.trim(),
        model: form.model.trim(),
        series: form.series.trim(),
        mileage: parseInt(form.mileage, 10),
        location_city: form.location_city.trim(),
        location_branch: form.location_branch.trim(),
        sale_price: parseFloat(form.sale_price),
        stock_number: form.stock_number.trim(),
        vin: form.vin.trim(),
        transmission: form.transmission.trim(),
        fuel_type: form.fuel_type.trim(),
        cylinders: parseInt(form.cylinders, 10),
        photos: form.photos.map((p) => p.trim()),
      };
      await api.post('/cars', carData);
      navigate('/admin/dashboard');
    } catch (err) {
      setErrors(['Erreur lors de la cration de la voiture.']);
    } finally {
      setSubmitting(false);
    }
  }

  return (
    <div style={{ maxWidth: 600, margin: 'auto', padding: '1rem' }}>
      <h1>Ajouter une nouvelle voiture</h1>
      <form onSubmit={handleSubmit} noValidate>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Anne
            <input
              type="number"
              name="year"
              value={form.year}
              onChange={handleChange}
              min="1886"
              max={new Date().getFullYear() + 1}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Marque
            <input
              type="text"
              name="make"
              value={form.make}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Modle
            <input
              type="text"
              name="model"
              value={form.model}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Srie
            <input
              type="text"
              name="series"
              value={form.series}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>
        <div style={{ marginBottom: '1rem' }}>
          <label>
            Kilomtrage
            <input
              type="number"
              name="mileage"
              value={form.mileage}
              onChange={handleChange}
              min="0"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Ville
            <input
              type="text"
              name="location_city"
              value={form.location_city}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Branche
            <input
              type="text"
              name="location_branch"
              value={form.location_branch}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Prix de vente
            <input
              type="number"
              name="sale_price"
              value={form.sale_price}
              onChange={handleChange}
              min="0"
              step="0.01"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Numro de stock
            <input
              type="text"
              name="stock_number"
              value={form.stock_number}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            VIN
            <input
              type="text"
              name="vin"
              value={form.vin}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Transmission
            <input
              type="text"
              name="transmission"
              value={form.transmission}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
              placeholder="Ex: Automatique, Manuelle"
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Type de carburant
            <input
              type="text"
              name="fuel_type"
              value={form.fuel_type}
              onChange={handleChange}
              required
              style={{ width: '100%', padding: '0.5rem' }}
              placeholder="Ex: Essence, Diesel"
            />
          </label>
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>
            Cylindres
            <input
              type="number"
              name="cylinders"
              value={form.cylinders}
              onChange={handleChange}
              min="1"
              required
              style={{ width: '100%', padding: '0.5rem' }}
            />
          </label>
        </div>

        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2">
            Photos
          </label>
          <input
            type="file"
            accept="image/*"
            multiple
            onChange={handlePhotoUpload}
            className="mb-4"
          />
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4">
            {form.photos.map((url, index) => {
              // S'assurer que l'URL est correctement formate pour l'affichage
              const imageUrl = url.startsWith('http') ? url : 
                             url.startsWith('/') ? `${window.location.origin}${url}` : 
                             `${window.location.origin}/${url}`;
              
              return (
                <div key={index} className="relative group">
                  <div className="relative overflow-hidden rounded-lg shadow-md h-48 bg-gray-100">
                    <img 
                      src={imageUrl}
                      alt={`Voiture ${index + 1}`}
                      className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-105"
                      onError={(e) => {
                        console.error('Erreur de chargement de l\'image:', {
                          src: e.target.src,
                          url: imageUrl,
                          response: e.nativeEvent
                        });
                        e.target.onerror = null;
                        e.target.src = 'data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22300%22%20height%3D%22200%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Crect%20width%3D%22300%22%20height%3D%22200%22%20fill%3D%22%23f0f0f0%22%2F%3E%3Ctext%20x%3D%2250%25%22%20y%3D%2250%25%22%20font-family%3D%22Arial%2C%20sans-serif%22%20font-size%3D%2214%22%20text-anchor%3D%22middle%22%20dy%3D%22.3em%22%20fill%3D%22%23666%22%3EImage%20non%20disponible%3C%2Ftext%3E%3C%2Fsvg%3E';
                      }}
                      onLoad={() => console.log('Image charge avec succs:', imageUrl)}
                    />
                  </div>
                  <button
                    type="button"
                    onClick={(e) => {
                      e.preventDefault();
                      removePhotoField(index);
                    }}
                    className="absolute top-2 right-2 bg-red-500 hover:bg-red-600 text-white rounded-full w-8 h-8 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-200 shadow-lg"
                    title="Supprimer cette photo"
                  >
                    
                  </button>
                </div>
              );
            })}
          </div>
          {form.photos.length === 0 && (
            <p className="text-sm text-gray-500 mt-2">
              Aucune photo slectionne. Veuillez tlverser au moins une photo.
            </p>
          )}
        </div>

        {errors.length > 0 && (
          <ul style={{ color: 'red', marginTop: '1rem' }}>
            {errors.map((err, i) => (
              <li key={i}>{err}</li>
            ))}
          </ul>
        )}

        <button type="submit" disabled={submitting} style={{ marginTop: '1rem', padding: '0.75rem 1.5rem' }}>
          {submitting ? 'Cration en cours...' : 'Crer la voiture'}
        </button>
      </form>
    </div>
  );
}

export default CreateCar;
